import * as React from 'react';
import {ToggleListItem} from '@here/hds-togglelist';
import {HDSListItemProps} from '../List/HDSListItem.js';

// eslint-disable-next-line no-new
new ToggleListItem();

interface HDSToggleListItemProperties extends HDSListItemProps {
  expanded?: boolean;
}

const HDSToggleListItem = React.forwardRef((props: HDSToggleListItemProperties, ref) => {
  const {children, expanded, className, ...rest} = props;
  return (
    <hds-togglelist-item
      {...(expanded && {expanded})}
      {...(className && {class: className})}
      ref={ref}
      {...rest}
    >
      {children}
    </hds-togglelist-item>
  );
});

HDSToggleListItem.defaultProps = {
  expanded: false,
};

export default HDSToggleListItem;
